.project__container {
  width: 70%;
  margin: 100px auto;
  color: #fff;
  padding: 120px 40px;
  text-align: center;
}
.project__container-title h1 {
  color: #fff;
  font-family: "Oregon LDO";
  font-size: 140px;
}
.project__container-subtitle {
  margin: 20px 0;
  font-family: "Neue Montreal";
  font-size: 16px;
}
.project__container-images {
  position: relative;
  width: 100%;
  height: 2000px;
  padding: 40px;
}
.project__image {
  width: 600px;
  height: 500px;
  margin: 40px auto;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
}
.project__image-one {
  background-image: url("../Assets/img-one.jpeg");
}
.project__image-two {
  position: relative;
  top: -100px;
  left: -250px;
  background-image: url("../Assets/img-two.jpeg");
}
.project__image-three {
  position: relative;
  left: 300px;
  background-image: url("../Assets/img-three.jpeg");
}
.project__image-four {
  position: relative;
  top: -200px;
  left: -100px;
  background-image: url("../Assets/img-four.jpeg");
}
.project-next-link {
  position: relative;
  width: 100%;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.project-next-link a {
  color: #fff;
  text-decoration: none;
  font-family: "Oregon LDO";
  font-size: 160px;
}

.anime-js.letter {
  display: inline-block;
  line-height: 1em;
}

@media (max-width: 900px) {
  .project__container-title h1 {
    font-size: 40px;
  }
  .project__container {
    width: 80%;
    height: auto;
  }
  .project__container-images {
    width: 100%;
    margin: 0 auto;
  }
  .project__image {
    width: 90%;
    position: relative;
    top: 0;
    left: 0;
  }
}
.letter {
  display: inline-block;
}